/* global context, dispatch */

import Vue from "vue";
import App from "./layouts/App.vue"
import store from "./store"
import router from "./router"
import Helpers from "./utils/Helpers";
import VueMask from "v-mask";
import SlideUpDown from "vue-slide-up-down";
// import VueImg from "v-img";
import VueLazyload from "vue-lazyload";
import renderVueComponentToString from 'vue-server-renderer/basic';

// Vue.use(VueImg);
Vue.use(VueMask);
Vue.use(Helpers);
Vue.component("slide-up-down", SlideUpDown);

Vue.use(VueLazyload);

const appVue = new Vue({
  store,
  router,
  data() {
    return {
      header_top: 0,
      header_height: 150,
      tabletBreakpoint: 1023,
      mobileBreakpoint: 767,
      width: 1920,
      height: 1080,
      ym_counter: null,
      recaptcha_key: null
    };
  },
  watch: {},
  computed: {
    isTablet() {
      return this.width <= this.tabletBreakpoint;
    },
    isMobile() {
      return this.width <= this.mobileBreakpoint;
    }
  },
  async created() {
    this.$router.push(context.url);
    const data = context.alem;

    this.$store.commit('setData', data);
  },
  mounted() {},
  methods: {
    reachGoal(goal) {
      if (typeof ym !== 'undefined') {
        ym(this.ym_counter, 'reachGoal', goal);
        console.log(goal);
      }
    },
    getRecaptchaToken(action) {
      return new Promise((resolve, reject) => {
        if (typeof grecaptcha === 'undefined') {
          return resolve(false);
        }
        grecaptcha.ready(() => {
          grecaptcha.execute(this.recaptcha_key, {
            action: action
          }).then((token) => {
            return resolve(token);
          }, (error) => {
            return resolve(false);
          });
        })
      })
    },
    async getDataAjax(route = '/', config = {}) {
      const response = await axios.get(route, config).catch(function (error) {
        return error.response;
      });

      return Promise.resolve(response.data);
    }
  },
  render: h => h(App),
})

renderVueComponentToString(appVue, (err, html) => {
  if (err) {
    throw new Error(err);
  }
  dispatch(html);
});
